.work {
    padding: 5% 10% 10% 10%;
}

.flowDesc {
    margin-left: 25%;
    padding-bottom: 3%;
}

.flowIcon {
    border: 1px solid #FFF;
    border-radius: 50%;
    padding: 2%;
    margin-left: -4.5%;
}

.flowBox {
    list-style: none;
    min-height: 50px;
    border-left: 2px solid #fff;
    position: relative;
    padding: 4%;
}

.flowBox::before {
    position: absolute;
    left: -15px;
    content: " ";
    border-radius: 500%;
    background: rgb(25, 207, 25);
    height: 30px;
    width: 30px;
}

.flowBox > h2 {
    display: inline;
}

.flowBox > i {
    margin-top: 0.5%;
    display: block;
}

.projects {
    display: flex;
    flex-direction: row;
}

.card {
    background-color: #1F1E1E;
    width: 50%;
    border: 1px solid transparent;
    border-radius: 10px;
    padding: 2%;
    margin: 2%;
}

.card > img {
    width: 100%;
    border-radius: 10px;
}

.githubLink {
    background-color: transparent;
    border: 1px solid rgb(25, 207, 25);
    color: rgb(38, 226, 38);
    border-radius: 10px;
    padding: 2%;
    cursor: pointer;
    margin-right: 2%;
}

@media screen and (max-width: 768px) {
    .work {
        padding: 20% 2% 2% 2%;
    }

    .flowDesc {
        margin-left: 2%;
    }

    .flowBox {
        padding: 7%;
    }

    .projects {
        flex-direction: column;
    }

    .card {
        margin: auto;
        width: 90%;
    }
}